.home{
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}

.home__image {
    width: 100%;
    background-position: center;
    background-size: cover;
    height: 350px;
    z-index: -1;
    margin-bottom: -100px;
    mask-image: linear-gradient(to bottom , rgba(0,0,0,1),rgba(0,0,0,0));
}
@media (max-width:767px){
    .home__image{
        max-height: 250px;
        background-size: cover;
        background-position: center;
        
    }
}
.home__row{
    display: flex;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    
}
@media (max-width:767px){
    .home__row{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        width: 80%;
    }
    .home__image{
       height: 300px;
    }
}
@media (max-width:991px){
    .home__row{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0 auto;
        width: 80%;
    }
    .home__row > img {
        z-index: -1;
    }
    
}