.checkoutProduct{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    
}
@media (max-width:767px){
    .checkoutProduct{
        display: flex;
        flex-direction: column;
    }
}
.checkoutProduct__info{
    padding-left: 20px;
}
.checkoutProduct__info > button{
    background-color: #ff9f00;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111;
    cursor: pointer;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
}
.starIcon{
    color:rgb(255, 196, 0);
}
.checkoutProduct__image{
    object-fit: contain;
    width: 180px;
    height: 180px;
}
@media (max-width:767px){
    .checkoutProduct__image{
        width: 100%;
    }
}
.checkoutProduct__rating{
    display: flex;
}
.checkoutProduct__title{
    font-size: 17px;
    font-weight: 700;
}