.productdetailsitem{
    display: flex;
    margin-top: 20px;
    margin-bottom: 20px;
    max-width: 90%;
    margin-bottom: 60px !important;
    
}


@media (max-width:767px){
    .productdetailsitem{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin: 0 auto;
    }
}
.productdetailsitem__info{
    padding-left: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 0 auto;
    text-align: center;
}
.productdetailsitem_btn{
    background-color: #ff9f00;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111;
    cursor: pointer;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    
}
.starIcon{
    color:rgb(255, 196, 0);
}
.productdetailsitem__image{
    object-fit: contain;
    width: 250px;
}
@media (max-width:767px){
    /* .productdetailsitem__image{
        width: 100%;
    } */
    .productdetailsitem__image{
        max-width: 160px;
    }
}
.productdetailsitem__rating{
    display: flex;
}
.productdetailsitem__title{
    font-size: 17px;
    font-weight: 700;
}
.productdetailsitem_detail{
    max-width: 70%;
}

.productdetailsitem_right{
    position: absolute;
    right: 0px;
    width: 150px;
    padding: 20px ;
    background-color: #f3f3f3;
    border: 1px solid #DDDDDD;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -ms-border-radius: 3px;
    -o-border-radius: 3px;
    overflow: hidden;

}
@media (max-width:767px){
    .productdetailsitem_right{
        position: relative;
        display: flex;
        flex-direction: column;
        margin-top: 50px;
        
    }
    
}
.productdetailsitem_right .text{
    text-align: center;
}
.productdetailsitem_right .text h6{
    color: #007185;
}

.productdetailsitem_right .text .location_icon{
    font-size: 13px;
    color: black;
}
.productdetailsitem_right .text p{
    font-size: 14px;
    color: #007185;
}