.header {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #131921;
  position: sticky;
  overflow: hidden;
  top: 0;
  z-index: 100;
}

.header__logo {
  display: flex;
  align-items: center;
  color: #ff9f00;
  margin: 0 25px;
}
.header__logoImage {
  margin-right: 10px;
  font-size: 24px;
}
@media (max-width: 767px) {
  .header__logo {
    margin: 0;
  }
}

.header__logo > img {
  margin-top: 8px;
  max-width: 100px;
}
@media (max-width: 767px) {
  .header__logo > img {
    margin-top: 8px;
    max-width: 68px;
  }
}
@media (max-width: 767px) {
  .header__logoTitle {
    margin-right: 5px;
    font-size: medium;
  }
}
.header__search {
  display: flex;
  flex: 1;
  align-items: center;
  border-radius: 24px;
  -webkit-border-radius: 24px;
  -moz-border-radius: 24px;
  -ms-border-radius: 24px;
  -o-border-radius: 24px;
  margin: 0 10px;
}
.header__searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}
.header__searchInput:focus {
  outline: none;
}
.header__searchInput:focus::placeholder {
  opacity: 0;
  transition: 1s ease-in-out;
}

.header__searchIcon {
  padding: 5px;
  height: 22px !important;
  background-color: #ff9f00;
  cursor: pointer;
}
.header__nav {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 767px) {
  .header__nav {
    display: flex;
    justify-content: flex-end;
    margin-left: 55px;
  }
}

.nav__item {
  display: flex;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  color: #fff;
}

.nav__itemLineOne {
  font-size: 10px;
}
.nav__itemLineTwo {
  font-size: 13px;
  font-weight: 800;
  display: flex;
}
@media (max-width: 767px) {
  .nav__itemLineOne,
  .nav__itemLineTwo {
    font-size: 8px;
  }
}
.nav__itemBasket {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  color: #fff;
  margin-right: 50px;
  margin-left: 10px;
}
.nav__basketCount {
  margin-left: 10px;
  margin-right: 10px;
  font-size: 16px;
  color: #ff9f00;
}

.basket__icon {
  font-size: 24px;
}
.basket__icon:hover {
  color: #ededed;
}

.burgermenu {
  display: none;
  position: absolute;
  right: 0 !important;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;

}
/* Start Mobile and Tablet for menu burger */
@media (max-width: 991px) {
  .burgermenu {
    display: block;
  }
  .header__nav {
    display: none;
  }
  .mob__nav {
    display: flex;
    position: fixed;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    background-color: #131921;
    color: #fff;
    top: 0;
    left: 0;
    width: 80%;
    height: 100%;
    z-index: 1200;
    transition: 1s ease-in-out;
  }

  .mob__nav > img {
    padding-top: 20px;
    margin: 0 auto;
    max-width: 100px;
  }
  .mob__nav .mob__nav__list {
    list-style: none;
    padding-top: 100px;
  }
  .mob__nav .mob__nav__list li {
    margin-bottom: 50px;
    font-size: 18px;
  }
  .header__searchInput {
    width: 50%;
    margin: 0 auto;
  }
  .header__searchIcon {
    display: none;
  }
  .mob__nav .basket__icon{
    font-size: 24px;
  }
  .mob__nav .nav__basketCount{
      font-size: 22px;
  }
  .header__logoImage{
    font-size: 22px;
  }
  .user{
      font-size: 22px;
      margin-left: 10px;
  }
  .menu__mask{
    display: flex;
    position: fixed;
    top: 0;
    right: 0;
    width: 20%;
    height: 100%;
    z-index: 1201;
  }
}
