.product__details{
    display: flex;
    padding: 20px;
    background-color:#FFF;
    height: 100%;
    min-height: 600px;
}

.product_ad{
    width: 100%;
    margin-bottom: 10px;
}
.product_title{
    margin-right: 10px;
    padding: 10px;
    border-bottom: 1px solid lightgray;
}
.product_left {
    display: flex;
    flex-direction: column;
}
@media (max-width:767px){
    .product{
        display: flex;
        flex-direction: column;
        
    }
}