.footer{
    position: relative;
    background-color:#131921;
    color: white;
    height: 100%;
    width:100%;
    left:0px;
    bottom:0px;
    z-index: 0;
}
.footer .container {
    display: grid;
    grid-template-columns: repeat(auto-fit , minmax(250px , 1fr));
    grid: 40px;
    place-items: center;

}
.footer .container .list h4{
    margin-bottom: -1px;
}
.footer .container ul li{
    text-decoration: none;
    list-style: none;
    margin-top: 8px;
    cursor: pointer;
}

@media (max-width:767px){
    .footer .container .list h4,
    .footer .container ul li{
        text-align: center;
    }
}
.footer .logo{
    display: flex;
    align-items: center;
    justify-content: center;
}
.footer .logo .buttons{
    display: flex;
    align-items: center;
    cursor: pointer;
}
@media (max-width:767px){
    .footer .logo,
    .footer .logo .buttons{
        display: flex;
        flex-direction: column;
    }
}
hr{
    border-top: 1px solid #3a4553;
    margin-top: 40px;
}
.footer .buttons > p{
    margin-bottom: 5px;
    margin-left: 15px;
    border: 1px solid #3a4553;
    padding: 8px 12px;

}
.footer .world-icon{
    margin-right: 5px;
    font-size: 12px;
}

.footer .logo > img{
    margin-top: 20px;
    max-width: 100px;
}
.footer .copyright{
    display: flex;
    justify-content: center;
    align-items: center;
}
.footer .copyright > p{
    text-align: center;
}
.footer .copyright span{
    color:#ff9f00 ;
}