.product{
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: #FFF;
    z-index: 1;
    border-radius: 8px;
    -webkit-border-radius: 8px;
    -moz-border-radius: 8px;
    -ms-border-radius: 8px;
    -o-border-radius: 8px;
}

.product__rating{
    display: flex;
    color:rgb(255, 196, 0);
}
.product  .product__image {
    max-width: 200px;
    width: 100%;
    object-fit: contain;
    margin: 20px 0 0 15px;
}

@media (max-width:767px){
    .product .product__image {
       width: 200px;
       padding-top: 30px;
       margin-top: 60px;
    }
    .product .product__buttons{
        display: flex;
        margin-top: 10px;
    }
}

 .product .product__buttons{
    display: flex;
}

.product .product__buttons .product_btn{
    background-color: #ff9f00;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color:#111;
    cursor: pointer;
    padding: 0 15px;
    border-radius: 4px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    -ms-border-radius: 4px;
    -o-border-radius: 4px;
    text-align: center;
    margin: 10px;
}

@media (max-width:767px){
    .product .product__buttons{
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }
}

.product__price{
    margin-top: 5px;
}
.product__info{
    height: 80px;
    margin-bottom: 15px;

}

@media (max-width:767px){
    .product > img {
        max-width: 200px;
        width: 100%;
        object-fit: contain;
        margin: 20px 0 0 15px;
    }
    .product__info{
        max-width: 100%;
        max-height: 100%;
        margin-bottom: -20px;
    
    }
    .product__price{
        margin:0;
    }
    
}
